function App() {
    /* 1. Define state variable */
    const [showForm, setShowForm] = useState(false);
    const [facts, setFacts] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [currentCategory, setCurrentCategory] = useState("all");
    useEffect(() => {
        async function getFacts() {
            setIsLoading(true);
            let query = supabase.from("facts").select("*");
            if (currentCategory !== "all")
                query = query.eq("category", currentCategory);
            const {data: facts, error} = await query
                // .from("facts")
                // .select("*")
                // // .eq("category", "technology")
                .order("votesInteresting", {ascending: false})
                .limit(1000);
            if (!error) setFacts(facts);
            else alert(`There was a problem getting data`);
            setIsLoading(false);
        }
        getFacts().then();
    }, [currentCategory]);
    return (
        <>
            <Header showForm={showForm} setShowForm={setShowForm}/>
            {/* Form */}
            {/* 2. Use state variable */}
            {showForm ? (
                <NewFactForm setFacts={setFacts} setShowForm={setShowForm}/>
            ) : null}
            <main className="main">
                {/* Aside (category filter) */}
                <CategoryFilter setCurrentCategory={setCurrentCategory}/>

                {/* Fact list */}
                {isLoading ? <Loader/> : <FactList facts={facts} setFacts={setFacts}/>}
            </main>
        </>
    );
}
function Loader() {
    return <p className="message">Loading...</p>;
}
class Header extends Component {
    render() {
        let {showForm, setShowForm} = this.props;
        const appTitle = "Today I Learned";
        return (
            <header className="header">
                <div className="logo">
                    <img src={logoImage} alt="today i learned logo"/>
                    <h1>{appTitle}</h1>
                </div>
                {/* 3. Update state variable */}
                <button
                    className="btn btn-large btn-open"
                    onClick={() => setShowForm((prevShow) => !prevShow)}
                >
                    {showForm ? "Close" : "Share a fact"}
                </button>
            </header>
        );
    }
}
Header.propTypes = {
    showForm: PropTypes.any,
    setShowForm: PropTypes.any,
};
const CATEGORIES = [
    {name: "technology", color: "#FF7B54"},
    {name: "science", color: "#425F57"},
    {name: "finance", color: "#355764"},
    {name: "society", color: "#64868E"},
    {name: "entertainment", color: "#856C8B"},
    {name: "health", color: "#809A6F"},
    {name: "history", color: "#F97B22"},
    {name: "news", color: "#65647C"},
];
function isValidHttpUrl(string) {
    let url;
    try {
        url = new URL(string);
    } catch (_) {
        return false;
    }
    return url.protocol === "http:" || url.protocol === "https:";
}
function NewFactForm({setFacts, setShowForm}) {
    const [text, setText] = useState("");
    const [source, setSource] = useState("");
    const [category, setCategory] = useState("");
    const [isUploading, setIsUploading] = useState(false);
    const textLength = text.length;
    async function handleSubmit(e) {
        // 1. prevent browser reload
        e.preventDefault();
        console.log(text, source, category);
        // 2. Check if data is valid. If so, create a new fact
        if (text && isValidHttpUrl(source) && category && textLength <= 200) {
            // 3. Create a new fact object
            // const newFact = {
            //     id: Math.round(Math.random() * 10000000),
            //     text,
            //     source,
            //     category,
            //     votesInteresting: 0,
            //     votesMindblowing: 0,
            //     votesFalse: 0,
            //     createdIn: new Date().getFullYear(),
            // };

            // 3. Upload fact to supabase and receive the new fact object
            setIsUploading(true);
            const {data: newFact, error} = await supabase
                .from("facts")
                .insert([{text, source, category}])
                .select();
            setIsUploading(false);

            // 4. Add the new fact to te UI: add the fact to state
            if (!error) setFacts((facts) => [newFact[0], ...facts]);

            // 5. Reset input fields
            setText("");
            setSource("");
            setCategory("");

            // 6. Close the form
            setShowForm(false);
        }
    }
    return (
        <form className="fact-form" onSubmit={handleSubmit}>
            <input
                type="text"
                placeholder="Share a fact with the world..."
                value={text}
                onChange={(e) => setText(e.target.value)}
                disabled={isUploading}
            />
            <span>{200 - textLength}</span>
            <input
                value={source}
                type="text"
                placeholder="Trustworthy source"
                onChange={(e) => setSource(e.target.value)}
                disabled={isUploading}
            />
            <select
                value={category}
                onChange={(e) => setCategory(e.target.value)}
                disabled={isUploading}
            >
                <option value="">Choose Category</option>
                {CATEGORIES.map((cat) => (
                    <option key={cat.name} value={cat.name}>
                        {cat.name.toUpperCase()}
                    </option>
                ))}
            </select>
            <button className="btn btn-large" disabled={isUploading}>
                Post
            </button>
        </form>
    );
}
function CategoryFilter({setCurrentCategory}) {
    return (
        <aside>
            <ul>
                <li className="category">
                    <button
                        className="btn btn-all-category"
                        onClick={() => setCurrentCategory("all")}
                    >
                        All
                    </button>
                </li>
                {CATEGORIES.map((cat) => (
                    <li key={cat.name} className="category">
                        <button
                            className="btn btn-category"
                            style={{backgroundColor: cat.color}}
                            onClick={() => setCurrentCategory(cat.name)}
                        >
                            {cat.name}
                        </button>
                    </li>
                ))}
            </ul>
        </aside>
    );
}
function FactList({facts, setFacts}) {
    if (facts.length === 0)
        return (
            <p className="message">
                No fact for this category yet! Create the first one 
            </p>
        );

    return (
        <section>
            <ul className="facts-list">
                {facts.map((fact) => (
                    <Fact key={fact.id} fact={fact} setFacts={setFacts}/>
                ))}
            </ul>
            <p className="fact-count">
                 There are {facts.length} facts dated in the database. Add your own! 
            </p>
        </section>
    );
}
function Fact({fact, setFacts}) {
    const [isUpdating, setIsUpdating] = useState(false);
    const isDisputed = fact.votesInteresting + fact.votesMindblowing < fact.votesFalse;
    async function handleVote(columnName) {
        setIsUpdating(true);
        const {data: updatedFact, error} = await supabase
        .from("facts")
        .update({[columnName]: fact[columnName] + 1})
        .eq("id", fact.id)
        .select();
        setIsUpdating(false);
        if (!error) setFacts((facts) => facts.map((f) => f.id === fact.id ? updatedFact[0] : f));
    }
    // fact.votesInteresting = undefined;
    // fact.votesMindblowing = undefined;
    // fact.votesFalse = undefined;
    return (
        <li className="fact">
            <p>
                {isDisputed ? <span className="disputed">
                    [ DISPUTED]
                </span> : null}
                {fact.text}
                <a
                    className="source"
                    href={fact.source}
                    target="_blank"
                    rel="noreferrer"
                >
                    Source
                </a>
            </p>
            <span
                className="tag"
                style={{
                    backgroundColor: (
                        CATEGORIES.find((cat) => cat.name === fact.category) || {
                            color: "#defaultColor",
                        }
                    ).color,
                }}
            >
        {fact.category}
        </span>
            <div className="vote-buttons">
                <button
                    onClick={() =>
                        handleVote("votesInteresting")}
                    disabled={isUpdating}>
                     {fact.votesInteresting}
                </button>
                <button
                    onClick={() =>
                        handleVote("votesMindblowing")}
                    disabled={isUpdating}>
                {fact.votesMindblowing}
                </button>
                <button
                    onClick={() =>
                        handleVote("votesFalse")}
                    disabled={isUpdating}
                >{fact.votesFalse}
                </button>
            </div>
        </li>
    );
}
export default App;
